import React,{useState,useEffect} from 'react'
import Helmet from 'react-helmet';
import logo from '../images/anime.gif'
import {
    Link
  } from "react-router-dom";

export default function Enter() {

  const [widths, setWindowWidth] = useState(0)
useEffect(() => { 

  updateDimensions();

  window.addEventListener('resize', updateDimensions);
  return () => 
    window.removeEventListener('resize',updateDimensions);
 }, [])

 const updateDimensions = () => {
   const widths = window.innerWidth
   setWindowWidth(widths)
   console.log(widths);

 }
 const responsive = {
  size_change: widths > 1000
  
}


  return (
    <div>
        <Helmet bodyAttributes={{style: 'background-color : #ff48b0'}}/>
        <div className='center_dick'>
        {responsive.size_change?
        <Link 
to="/home" > 
          <img className='center_dick_img'
            src={logo}
            alt="example"
        
          />
        </Link> :
        <Link 
        to="/home" > 
                  <img className='center_dick_img'
                    src={logo}
                    alt="example"
                
                    width='382'
                    height='289'
                  />
                  </Link>}
        </div>
    </div>
  )
}
